
body{
  background-image:url(/src/photos/bg-intro-desktop\ \(1\).png);
  background-color:hsl(0, 100%, 74%) ;
  font-size: 16px;
  
}


.grid{
  display:grid;
  height:700px;
  grid-template-columns: 450px  500px;
  column-gap: 50px;
  margin-top: 50px;
  padding-left: 120px;
  grid-template-areas:
  "left"
  "right";
}




.left h1{
  color:white;
  margin-top:200px;
  font-weight: 700;
  line-height: 40px;
  font-size:40px;

}

.left p{
  color:white;
}

.form{
  background-color:white;
  height:500px;
  margin-top: 30px;
  border-radius: 10px;
}

.button{
  width:400px;
  color:white;
  background-color:hsl(248, 32%, 49%);
  border-radius:10px;
  padding:18px;
  margin-left:50px;
  border-style:none;
  font-size:16px;
}





.button2{
   width:400px;
   background-color:hsl(154, 59%, 51%);
   border-radius: none;
   margin-left:50px;
   margin-top:15px;
   color:white;
   padding:15px;
   border-style:none;
   border-radius:5px;
   font-weight:600;
   font-size:16px;

}

input{
  margin-left: 30px;
  margin-right:auto;
  width: 400px;
  padding:18px;
  border-radius: 10px;
  margin-top: 30px;
  border-color:lightgray;
  
}

.form p{
  text-align: center;
}

.form p{
  color:gray;
}

a{
  text-decoration: none;
}